import React from 'react'

/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Formik Lib ========== */
import * as Yup from "yup";
import { Formik} from "formik"


function Glucose({ setGlucose, fun }) {
    const hendleSubmit = (val) => {
        const data = {
            id: 4,
            reading1: val.Glucose,
            value: 'gl'
        }
        fun(data)
        setGlucose(false)

    }
    return (
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            fullWidth
            maxWidth="sm">
            <DialogTitle>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add Glucose</Typography>
                    <IconButton onClick={() => setGlucose(false)}>
                        <CloseIcon />
                    </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent sx={{ paddingTop: 0 }}>
                <Container>
                    <Formik onSubmit={hendleSubmit} initialValues={{}}
                        validationSchema={Yup.object().shape({
                            Glucose: Yup.number().required('Enter Number')
                        })}>
                        {({ errors, handleBlur,
                            handleSubmit,
                            handleChange,
                            touched }) => (
                            <form onSubmit={handleSubmit}>
                                <Box>
                                    <Typography>Glucose<span style={{ color: 'red' }}>*</span></Typography>
                                    <TextField name="Glucose" margin="normal" onBlur={handleBlur} onChange={handleChange} error={Boolean(touched.Glucose && errors.Glucose)} helperText={touched.Glucose && errors.Glucose} />
                                </Box>
                                <Button
                                    variant="contained"
                                    type="submit"
                                >Submit</Button>
                            </form>
                        )}
                        </Formik>
                </Container>
            </DialogContent>
        </Dialog>
    )
}

export default Glucose
