import React, { useEffect,useState } from 'react';
import { connect } from 'react-redux'
import { useSelector } from 'react-redux';
import { report } from "src/redux/actions";
import classes from "src/styles/reports/reports.module.scss";
import RenderFiles from "src/components/report/renderFile";
import PermMediaIcon from '@material-ui/icons/PermMedia';
import getParameterByName from "src/utils/getSearchParams";
import { useNavigate, createSearchParams } from "react-router-dom";
import { Helmet } from "react-helmet";
import AddReportForm from "src/components/report/addReportForm";
import TabPanel from "src/utils/tabPanel";
import a11yProps from "src/utils/a11yProps";
import EmployeeReportView from 'src/components/report/employeeReportView';
import {
    Grid,
    Typography,
    Box,
    CircularProgress,
    Container,
    Tab,Tabs
  } from "@material-ui/core";


function AddReport({ employeeReport, fetch_Employee_reports }) {    
  const [value, setValue] = useState(0);
  const [addNew, SetAddNew] = useState(false);
  const query = getParameterByName("tab");
  const navigate = useNavigate();
  

const handleChange = (event, newValue) => {
    setValue(newValue);
    navigate({
      pathname: window.location.pathname,
      search: `?${createSearchParams({
        tab: newValue
      })}`
    })
  };

  const item={

  }
  useEffect(() => {
   
    if (query === null) {
      navigate({
        pathname: window.location.pathname,
        search: `?${createSearchParams({
          tab: value
        })}`
      })
    } else {
      /**
       *  
       * We have tab number for now 2 in [0, 1] format
       * Now Check if tab number is greater than tab length
       * 
       */
      if (Number(query) >= 2) {
        // If tab number is greater than tab length 
        // push the tab to the 0.
        navigate({
          pathname: window.location.pathname,
          search: `?${createSearchParams({
            tab: 0
          })}`
        })
        setValue(0)

      } else {
        setValue(Number(query))
      }
    }
  }, [value])

  useEffect(()=>{

    fetch_Employee_reports()
  },[])


    return (
        <div>
            <Box
        sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}>
        <Box>
          <Container maxWidth={false} className={classes.rt__root}>
            <Box className={classes.rt__header} >
              <Typography className={classes.rt__title}>
                Reports
              </Typography>
            </Box>
          </Container>
        </Box>
        <Container maxWidth={false} className={classes.fm__root}>
          <Box mt={3}>
            <Tabs
              value={value}
              onChange={handleChange}
              aria-label="report tabs"
              variant="scrollable"
              className={classes.tab__root}
            >
              <Tab
                label="Report"
                {...a11yProps(0)}
                classes={{
                  root: classes.tabs,
                  selected: classes.tab__selected
                }}
              />
              <Tab
                label="Add Report"
                {...a11yProps(1)}
                classes={{
                  root: classes.tabs,
                  selected: classes.tab__selected
                }}
              />
            </Tabs>
            <TabPanel value={value} index={0}>
              <EmployeeReportView getEmployeeReport={fetch_Employee_reports} />
              {/* <RenderFiles
                item={item}
                classes={classes}
                results={employeeReport.results} 
              /> */}
            </TabPanel>
            <TabPanel value={value} index={1}>
              <AddReportForm />
            </TabPanel>
          </Box>
        </Container>
      </Box>
      <Helmet>
        <title>Report | TraceLyfe</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}
      >
      </Box>
        </div>
    )
}
const mapStateToProps = (state) => ({
    employeeReport: state.report.employeeReport
})

const mapDispatchToProps = (dispatch) => ({
    fetch_Employee_reports:()=> dispatch(report.fetch_Employee_reports())
})
export default connect(mapStateToProps, mapDispatchToProps)(AddReport)
