import React, { useState } from 'react';

/* ========= Material ui ========== */
import { Tabs, Tab } from "@material-ui/core";

/* ========= Style ========== */
import classes from "src/styles/dashboard/dashboard.module.scss";

/* ========= Utils Data ========== */
import a11yProps from "src/utils/a11yProps";
import TabPanal from "src/utils/tabPanel";

/* ========= Tab Data ========== */
import BloodGlucose from "./Tabs_/BloodGlucose";
import BodyTempreature from "./Tabs_/BodyTempreature";
import S02 from "./Tabs_/S02";
import Pulse from "./Tabs_/Pulse";
import BodyWeight from "./Tabs_/BodyWeight";
import BloodPre from "./Tabs_/BloodPre";

const EmployeeTab = ({ data }) => {
    const [value, setValue] = useState(0)
    const blood_pressure = data.blood_pressure;
    const oxygen_saturation = data.oxygen_saturation;
    const tempreature = data.temperature;
    const weight = data.weight;
    const pulse = data.pulse;
    const glucose = data.glucose

    const handleChange = (e, val) => {
        setValue(val)
    }
    return (
        <div>
            <Tabs
                value={value}
                onChange={handleChange}
                aria-label="report tabs"
                variant="scrollable"
                className={classes.tab__root}
            >
                <Tab
                    label=" Body Weight"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
                <Tab
                    label="Blood Glucose"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
                <Tab
                    label="Body Tempreature"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
                <Tab
                    label="Blood Pressure"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
                <Tab
                    label="Heart Rate"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
                <Tab
                    label="Oxygen Saturation"
                    {...a11yProps(0)}
                    classes={{
                        root: classes.tabs,
                        selected: classes.tab__selected
                    }}
                />
            </Tabs>
            <TabPanal value={value} index={0}>
                <BodyWeight data={weight} />
            </TabPanal>
            <TabPanal value={value} index={1}>
                <BloodGlucose data={glucose} />
            </TabPanal>
            <TabPanal value={value} index={2}>
                <BodyTempreature data={tempreature} />
            </TabPanal>
            <TabPanal value={value} index={3}>
                <BloodPre data={blood_pressure} />
            </TabPanal>
            <TabPanal value={value} index={4}>
                <Pulse data={pulse} />
            </TabPanal>
            <TabPanal value={value} index={5}>
                <S02 data={oxygen_saturation} />
            </TabPanal>
        </div>
    )
}

export default EmployeeTab

