import React from 'react'
/* ========= Material ui ========== */
import { Box, Container, Typography, Grid, } from "@material-ui/core";

/* ========= Style ========== */
import classes from "src/styles/dashboard/dashboard.module.scss";

/* ========= Chart ========== */
import EmployeeAreaChart from "src/__charts__/EmployeeAreaChart";
import EmployeeTestChart from 'src/__charts__/EmployeeTestChart';



function BodyWeight({ data }) {
    let new_data;
    if(data){
         new_data = data[0];
    }
    return (
        <div>
            {
                data ? 
                <Box className={classes.tab__root}>
                <Box className={classes.tab__header}>
                    <Container>
                        <Box className={classes.tab__box}>
                            <Grid container>
                                <Grid item sx={12} sm={12} md={3} className={classes.tab__grid}>
                                    <Box className={classes.tab__data}>
                                        <Typography className={classes.title}>
                                            Body Weight
                                        </Typography>
                                        <br />
                                        <br />
                                        <Typography className={classes.text}>
                                            Last Reading
                                        </Typography>
                                        <Typography className={classes.unit}>
                                            {new_data.reading1} Kg
                                        </Typography>
                                        <Typography className={classes.date}>
                                            {new_data.created_on}
                                        </Typography>
                                    </Box>
    
                                </Grid>
                                <Grid item sx={12} sm={12} md={9} >
                                    <Typography>
                                        <EmployeeAreaChart data={data} />
                                    </Typography>
                                </Grid>
                            </Grid>
                        </Box>
                    </Container>
                </Box>
            </Box>:
              <Box className={classes.tab__root}>
              <Box className={classes.tab__header}>
                  <Container>
                      <Box className={classes.tab__box}>
                          <Grid container>
                              <Grid item sx={12} sm={12} md={3} className={classes.tab__grid}>
                                  <Box className={classes.tab__data}>
                                      <Typography className={classes.title}>
                                          Body Weight
                                      </Typography>
                                  </Box>
  
                              </Grid>
                              <Grid item sx={12} sm={12} md={9} >
                                  <Typography>
                                     No Data Found
                                  </Typography>
                              </Grid>
                          </Grid>
                      </Box>
                  </Container>
              </Box>
          </Box>

            }
      
        </div>
    )
}

export default BodyWeight
