/* ========= Material ui ========== */
import { Box, Button, Typography, Grid, Container } from '@material-ui/core';

/* ========= usestate ========== */
import { useState, useEffect } from "react"

/* ========= style ========== */
import classes from "src/styles/dashboard/dashboard.module.scss";


/* ========= React Redux ========== */
import { dashboard } from "../../redux/actions";
import { connect } from 'react-redux';


/* ========= Vital Table ========== */
import VitalTable from "./vital-table";

import EmployeeTab from "./EmployeeTab";
import AddVitals from "./AddVitals"

const healthStatus = {
  color: 'green',
  textTransform: 'uppercase',
  fontWeight: 'bolder',
  fontFamily: 'Helvetica'
};
function UserDetails({ profile, fetch_health_Status, health_status
  , fetch_vital_data, all_vital_data, fetch_data }) {
  const [open, setOpen] = useState(false);
  const data = profile.profile_id
  useEffect(() => {
    fetch_health_Status(data);
    fetch_vital_data(data)
  }, [])
  return (
    <div>
      <Box
        sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}
      >
        <Container maxWidth={false} className={classes.od__root}>
          <Box className={classes.od__header} >
            <Typography className={classes.od__title}>
              Hello , {profile.name}<br />
              <Typography className={classes.od__text}>
                Your Health status is : <span style={healthStatus}>{health_status}</span>
              </Typography>
            </Typography>
            <Typography
              sx={{
                display: "flex",
                alignItems: "center",
                color: "gray"
              }}
            >
              <Button
                variant="contained"
                sx={{
                  textTransform: 'capitalize'
                }}
                onClick={() => setOpen(true)}

              >Add Vitals</Button>
            </Typography>
          </Box>
        </Container>
      </Box>

      <Box>
        {open && <AddVitals open={open} setOpen={setOpen} fetch_data={fetch_data} profile={profile} />}
      </Box>
      <Box sx={{ color: "#0D809F" }}>
        {all_vital_data && <EmployeeTab data={all_vital_data} profile={profile} />}
      </Box>
    </div>
  )
}
const mapStateToProps = (state) => ({
  health_status: state.dashboard.health_status,
  all_vital_data: state.dashboard.all_vital_data,
});
const mapDispatchToProps = (dispatch) => ({
  fetch_health_Status: (data) => dispatch(dashboard.fetch_health_Status(data)),
  fetch_vital_data: (data) => dispatch(dashboard.fetch_vital_data(data)),

});

export default connect(mapStateToProps, mapDispatchToProps)(UserDetails)