import React from 'react'

/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Formik Lib ========== */
import { Formik } from "formik"
import * as Yup from "yup";


function Temp({ tempreature, setTempreature, fun }) {
    const hendleSubmit = (val) => {
        const data = {
            id: 2,
            reading1: val.Temp,
            value: "℉"
        }
        fun(data)
        setTempreature(false)
    }
    return (
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            fullWidth
            maxWidth="sm">
            <DialogTitle>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add Tempreature</Typography>
                    <IconButton onClick={() => setTempreature(false)}>
                        <CloseIcon />
                    </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent sx={{ paddingTop: 0 }}>
                <Formik onSubmit={hendleSubmit} initialValues={{}}
                    validationSchema={Yup.object().shape({
                        Temp: Yup.number().required('Enter Number')
                    })}>
                    {({ errors,
                        handleBlur,
                        handleSubmit,
                        handleChange,
                        touched }) => (
                        <form onSubmit={handleSubmit}>
                            <Box>
                                <Typography>Pulse<span style={{ color: 'red' }}>*</span></Typography>
                                <TextField name="Temp" margin="normal" onBlur={handleBlur} onChange={handleChange} error={Boolean(touched.Temp && errors.Temp)} helperText={touched.Temp && errors.Temp} />
                            </Box>
                            <Button
                                variant="contained"
                                type="submit"
                            >Submit</Button>
                        </form>
                    )}
                </Formik>
            </DialogContent>
        </Dialog>
    )
}

export default Temp
