import React, { useState } from 'react'

/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
    Grid,
    Avatar, Card, CardHeader, CardContent
} from "@material-ui/core";
import { toast } from "react-toastify";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Vital Data ========== */
import Temp from "./Vital/Temp"
import So2 from "./Vital/So2"
import Weight from "./Vital/Weight"
import Glucose from "./Vital/Glucose"
import HeartRate from "./Vital/HeartRate";
import BloodPressure from "./Vital/BloodPressure"

/* ========= Vital Data Icons ========== */
import GlucoseIcon from "src/__icons__/GlucoseIcon.svg";
import PulseIcon from "src/__icons__/PulseIcon.svg";
import TempreatureIcon from "src/__icons__/TempreatureIcon.svg";
import WeightIcon from "src/__icons__/WeightIcon.svg";
import So2Icon from "src/__icons__/So2Icon.svg";

/* ========= React Redux ========== */
import { useDispatch } from "react-redux";
import { dashboard } from "src/redux/actions"


function AddVitals({ open, setOpen, fetch_data, profile }) {
    const dispatch = useDispatch();
    const [tempreature, setTempreature] = useState(false)
    const [glucose, setGlucose] = useState(false)
    const [hRate, setHRate] = useState(false)
    const [so2, setSo2] = useState(false)
    const [weight, setWeight] = useState(false)
    const [bloodPressure, setBloodPressure] = useState(false)


    const vitalFun = (data) => {
        const vitals_data = {
            profile_id: profile.profile_id,
            vitals: [data]
        };
        dispatch(dashboard.post_vital_data(vitals_data)).then(res => {
            toast.success("Doctor has been added successfully.")
        }).catch(error => { toast.error("Network Error") })
    }

    return (
        <div>


            <Dialog
                open={open}
                onClose={() => setOpen(false)}
                fullWidth
                maxWidth="sm">
                <DialogTitle>
                    <Box display="flex" justifyContent="space-between" alignItems="center">
                        <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add Vitals</Typography>
                        <IconButton onClick={() => setOpen(false)}>
                            <CloseIcon />
                        </IconButton>
                    </Box>
                </DialogTitle>
                <DialogContent sx={{ paddingTop: 0 }}>
                    <Container>
                        <Grid container spacing={2}>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setTempreature(true)}>
                                        <img src={TempreatureIcon} onClick={() => setTempreature(true)} />
                                        <Box> <Typography>Add Tempreature</Typography></Box>
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setHRate(true)}>
                                        <img src={PulseIcon} />
                                        <Typography>Add Heart Rate</Typography>
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setWeight(true)}>
                                        <img src={WeightIcon} />
                                        <Typography>Add Weight</Typography>
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setGlucose(true)}>
                                        <img src={GlucoseIcon} />
                                        <Typography>Add GluCose</Typography>
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setSo2(true)}>
                                        <img src={So2Icon} />
                                        <Typography>Add So2</Typography>
                                    </Button>
                                </Box>
                            </Grid>
                            <Grid item xs={8} md={6}>
                                <Box>
                                    <Button onClick={() => setBloodPressure(true)}>
                                        <img src={So2Icon} />
                                        <Typography>Add Blood Pressure</Typography>
                                    </Button>
                                </Box>
                            </Grid>
                        </Grid>
                    </Container>
                </DialogContent>
                {tempreature && <Temp tempreature={tempreature} setTempreature={setTempreature} fun={vitalFun} />}
                {hRate && <HeartRate hRate={hRate} setHRate={setHRate} fun={vitalFun} />}
                {weight && <Weight weight={weight} setWeight={setWeight} fun={vitalFun} />}
                {glucose && <Glucose glucose={glucose} setGlucose={setGlucose} fun={vitalFun} />}
                {so2 && <So2 setSo2={setSo2} fun={vitalFun} />}
                {bloodPressure && <BloodPressure bloodPressure={bloodPressure} setBloodPressure={setBloodPressure} fun={vitalFun} setOpen={setOpen} />}
            </Dialog>
        </div>
    )
}

export default AddVitals
