import { useState, useRef } from "react";
import {
  Box,
  Grid,
  TextField,
  Typography,
  Button,
  Select,
  MenuItem,
  IconButton
} from "@material-ui/core";

import { useNavigate, createSearchParams } from "react-router-dom";

/* ========= React Redux ========== */
import { useDispatch } from "react-redux";
import { family } from "src/redux/actions";

/* ========= Related to the form ========= */ 
import * as Yup from "yup";
import { Formik } from "formik";
import moment from "moment";
import { toast } from "react-toastify";

/* ========= Stylesheet ========= */ 
import classes from "src/styles/familyMembers/familyMembers.module.scss"

const AddNewFamilyMemberForm = ({showFamilyMember}) => {

  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleNewFamilyMember = (values) => {

    if(values){

      // Appending data in the FormData();

      const formData = new FormData()
      formData.append("name", values["name"])
      formData.append("relation", values["relation"])
      formData.append("address", values["address"])
      formData.append("gender", values["gender"])
      formData.append("dob", values["dob"])
      console.log(formData);
      dispatch(family.addNewFamilyMember(formData))
        .then(res => {
          toast.success("Family Member has been added successfully.")
          showFamilyMember(false)
          navigate({
            pathname: window.location.pathname,
            search: `${createSearchParams({tab: 0})}`
          })
          console.log("Helllo WOOOO")
        })
        // .catch(err => {
        //   toast.error("Network Error: Please try again later.")
        // })
    } else {
      toast.error("Please fill the required field.")
    }
  }

  return(
    <Box p={2}>
      <Formik
        initialValues={{}}
        onSubmit={handleNewFamilyMember}
        validationSchema={Yup.object().shape({
          name: Yup.string().required("Full Name is required."),
          relation: Yup.string().required("Relation is required."),
          address: Yup.string().required("Address is required."),
          gender: Yup.string().required('Gender is required'),
          dob: Yup.string().required("Date of birth is required."),
        })}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          touched,
          values
        }) => (
          <form onSubmit={handleSubmit} >
            <Grid container spacing={3}>
              <Grid item xs={12} sm={12} md={6}>
                <Box>
                  <Typography>Family Member Name <span style={{color: 'red'}}>*</span></Typography>
                  <TextField 
                    fullWidth
                    name="name"
                    margin="normal"
                    placeholder="Name..."
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.name && errors.name)}
                    helperText={touched.name && errors.name}
                  />
                </Box>
                <Box mt={2}>
                  <Typography>Relation with you <span style={{color: 'red'}}>*</span></Typography>
                  <TextField 
                    fullWidth
                    name="relation"
                    margin="normal"
                    placeholder="Brother, Sister etc..."
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.relation && errors.relation)}
                    helperText={touched.relation && errors.relation}
                  />
                </Box>
                <Box mt={2}>
                  <Typography>Gender <span style={{color: 'red'}}>*</span></Typography>
                  <Select
                    displayEmpty
                    inputProps={{ 'aria-label': 'Without label' }}
                    fullWidth
                    name="gender"
                    margin="normal"
                    placeholder="Gender"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.gender && errors.gender)}
                    helperText={touched.gender && errors.gender}
                  >
                    <MenuItem value="male">Male</MenuItem>
                    <MenuItem value="female">Female</MenuItem>
                    <MenuItem value="other">Other</MenuItem>
                  </Select>
                </Box>
              </Grid> 
              <Grid item xs={12} sm={12} md={6}>
              <Box mt={2}>
                  <Typography>Address <span style={{color: 'red'}}>*</span></Typography>
                  <TextField 
                    fullWidth
                    name="address"
                    margin="normal"
                    placeholder="Address..."
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.address && errors.address)}
                    helperText={touched.address && errors.address}
                  />
                </Box>
                <Box>
                  <Typography>Date Of Birth <span style={{color: 'red'}}>*</span></Typography>
                  <TextField 
                    fullWidth
                    name="dob"
                    margin="normal"
                    placeholder="20-12-1994"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.dob && errors.dob)}
                    helperText={touched.dob && errors.dob}
                  />
                </Box>
              </Grid>
              <Grid item xs={12} sm={12} md={6}>
                <Button 
                  variant="contained"
                  fullWidth
                  className={classes.fm__form__btn}
                  type="submit"
                >
                  Add Family Member
                </Button>
              </Grid>
            </Grid>
          </form>
        )}
      </Formik>
    </Box>
  )
}

export default AddNewFamilyMemberForm;