import React, { useState } from 'react';
import SmartCalendar from "src/components/MySchedules/SmartCalendar"
import moment from "moment";
import classes from "src/styles/MySchedule/MySchedule.module.scss";
import {
  Box,
  Container,
  Typography,
  Button,
  Tab,
  Tabs,
  Card,
  CardHeader,
  CardContent,
  Divider
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import Buttons from "src/components/MySchedules/Buttons";
function index() {
  const [response, setResponse] = useState(moment())
  return (
    <div>
      <Box>
        <Box sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}>
          <Container maxWidth={false} className={classes.ms__root}>
            <Box className={classes.ms__header}>
              <Typography className={classes.ms__title}>
                My Schedule
              </Typography>
              <Typography
                sx={{
                  display: "flex",
                  alignItems: "center",
                  color: "gray"
                }}
              >
                <LocationOnIcon
                  sx={{
                    marginRight: 1
                  }}
                />
                New Delhi
              </Typography>
            </Box>
          </Container>
        </Box>
        <Box sx={{ mt: 4 }}>
          <Container maxWidth="xl">
            <Card>
              <CardHeader title="Calendar">
                <h1>Hello World</h1>
              </CardHeader>
              <Divider />
              <CardContent>
                <SmartCalendar response={response} />
              </CardContent>
            </Card>
          </Container>
        </Box>
      </Box>
    </div>

  )
}
export default index;