import axios from "axios"
import { GET_USER_PROFILE_DATA, GET_VITAL_DATA, GET_HEALTH_DATA } from "../constants/dashboard";
const DASHBOARD_BASE_URL = `${process.env.REACT_APP_API_URL}`;


let headers = { 'Content-Type': 'application/json' };

const userData = (user) => (dispatch, getState) => {
    localStorage.setItem('user', user.data.data.name)
    localStorage.setItem('profile_id',user.data.data.profile_id)
    localStorage.setItem('email',user.data.data.email)
    localStorage.setItem('id',user.data.data.id)
    localStorage.setItem('location',user.data.data.location)
    dispatch({ type: GET_USER_PROFILE_DATA, data: user.data.data })
}

const vitalData=(data)=>(dispatch,getState)=>{
    localStorage.setItem('Vitals',data.data.data)
    dispatch({ type: GET_VITAL_DATA, data: data.data.data})
}
export const fetchUserData = () => async (dispatch, getState) => {
    try {
        const token = getState().auth.access_token;
        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.get(`${DASHBOARD_BASE_URL}/employee/api/get-user-info/`, { headers });
            if (response.status == 200) {
                console.log("RESPONSE", response)
                dispatch(userData(response))
            }
        }
    }
    catch (error) {
        throw error
    }
}

export const fetch_vital_data = (data) => async (dispatch, getState) => {
    try {
        const token = getState().auth.access_token;
        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.get(`${DASHBOARD_BASE_URL}/employee/api/employee-vital-graph?from_date=01-09-21&profile_id=${data}`, { headers });
            if (response.status == 200) {
                dispatch(vitalData(response))    
            }
        }
    }
    catch (error) {
        throw error;
    }
}

export const fetch_health_Status = (data) => async (dispatch, getState) => {
    try {
        const token = getState().auth.access_token;
        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.get(`${DASHBOARD_BASE_URL}/employee/api/get-health-status?profile_id=${data}`, { headers })
            if (response.status == 200) {
                dispatch({ type: GET_HEALTH_DATA, data: response.data.data })
            }
        }
    }
    catch (error) {
        console.log("error found", error)
    }
}
export const post_vital_data = (data) => async (dispatch, getState) => {
    try {
        console.log("Post Vital data", data)
        const token = getState().auth.access_token;
        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.post(`${DASHBOARD_BASE_URL}/employee/api/add-vitals-manually/`, data, { headers })
            if (response.status == 200 || response.status == 201) {
                dispatch(fetch_vital_data(data.profile_id))
                console.log("DATA POSTED SUCCESFULLY ))))")
            }
        }
    }
    catch (error) {
        throw error
    }
}
