import axios from "axios";
import { toast } from "react-toastify";
import {
  GET_FAMILY_MEMBERS,
  SELECT_FAMILY_MEMBER,
  UPDATE_PAGE_NUM,
} from "../constants/family";
  
const FAMILY_BASE_URL = `${process.env.REACT_APP_API_URL}/employee/api`;
let headers = { 'Content-Type': 'application/json' };

export const addNewFamilyMember = (data) => async (dispatch, getState) => {
  try {
    console.log("WWWW",data)
    const token = getState().auth.access_token;
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
      const response = await axios.post(
        `${FAMILY_BASE_URL}/create-profile/`,
        data,
        { headers }
      );
      if (response.status === 200 || response.status === 201) {
        dispatch(getFamilyMembers())
        return response;
      }
    }
  } catch (error) {
    if (error.response) {
      toast.error(error.response.data.message)
    } else {
      toast.error("Network Error: Please try again later.")
    }
    throw error;
  }
};

export const getFamilyMembers = () => async (dispatch, getState) => {
  try {
    const token = getState().auth.access_token;
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
      const response = await axios.get(
        `${FAMILY_BASE_URL}/user-family-profiles/`,
        { headers }
      );
      if (response.status === 200) {
        console.log("Family Members Data")
        dispatch({ type: GET_FAMILY_MEMBERS, data: response.data.data })
      }
    }
  } catch (error) {
    throw error;
  }
};

export const selectFamilyMemberProfile = (data) => async (dispatch, getState) => {
  try {
    const token = getState().auth.access_token;
    if (token) {
      headers['Authorization'] = `Bearer ${token}`;
      const response = await axios.get(
        `${FAMILY_BASE_URL}/get-user-info/?profile_id=${data}`,
        { headers }
      );
      console.log("Selected Family Members Data")
      if (response.status === 200) {
        console.log("Select Family Member Profile")
        dispatch({ type: SELECT_FAMILY_MEMBER, data: response.data.data })
      }
    }
  } catch (error) {
    throw error;
  }
};

export const updatedPageNum = (data) => (dispatch, getState) => {
  if (data) {
    dispatch({ type: UPDATE_PAGE_NUM, data: data })
  }
}