import React from 'react'

/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Formik Lib ========== */
import { Formik } from "formik";
import * as Yup from "yup";


function So2({ setSo2, fun }) {

    const hendleSubmit = (val) => {
        const data = {
            id: 25,
            reading1: val.So2,
            value: "O"
        }
        fun(data)
        setSo2(false)
    }
    return (
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            fullWidth
            maxWidth="sm">
            <DialogTitle>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add So2</Typography>
                    <IconButton onClick={() => setSo2(false)}>
                        <CloseIcon />
                    </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent sx={{ paddingTop: 0 }}>
                <Container>
                    <Formik onSubmit={hendleSubmit} initialValues={{}}
                        validationSchema={Yup.object().shape({
                            So2: Yup.number().required('Enter Number')
                        })}>
                        {({ errors, handleBlur,
                            handleSubmit,
                            handleChange,
                            touched }) => (
                            <form onSubmit={handleSubmit}>
                                <Box>
                                    <Typography>So2<span style={{ color: 'red' }}>*</span></Typography>
                                    <TextField name="So2" margin="normal" onBlur={handleBlur} onChange={handleChange} error={Boolean(touched.So2 && errors.So2)} helperText={touched.So2 && errors.So2} />
                                </Box>
                                <Button
                                    variant="contained"
                                    type="submit"
                                >Submit</Button>
                            </form>
                        )}
                    </Formik>

                </Container>
            </DialogContent>
        </Dialog>
    )
}

export default So2
