import { useState, useRef, useEffect } from "react";
import {
  Box,
  Grid,
  TextField,
  Typography,
  Button,
  Select,
  MenuItem,
  IconButton,
  Input
} from "@material-ui/core";

/* ========= React Redux ========== */
import { useDispatch,useSelector } from "react-redux";
import { report } from "src/redux/actions";
import { connect } from 'react-redux';

/* ========= Related to the form ========= */
import * as Yup from "yup";
import { Formik } from "formik";
import moment from "moment";
import { toast } from "react-toastify";
import { render } from 'react-dom';

/* ========= Stylesheet ========= */
import classes from "src/styles/familyMembers/familyMembers.module.scss"



const AddReportForm = ({ post_employee_report }) => {
  const [selectedFile, setSelectedFile] = useState()
  const [preview, setPreview] = useState()
  const profile=useSelector((state)=>state)
  console.log("Profile",profile)

  useEffect(() => {
    if (!selectedFile) {
      setPreview(undefined)
      return
    }

    const objectUrl = URL.createObjectURL(selectedFile)
    setPreview(objectUrl)

    // free memory when ever this component is unmounted
    return () => URL.revokeObjectURL(objectUrl)
  }, [selectedFile])

  const onSelectFile = e => {
    if (!e.target.files || e.target.files.length === 0) {
      setSelectedFile(undefined)
      return
    }

   
    // I've kept this example simple by using the first image instead of multiple
    setSelectedFile(e.target.files[0])
  }
  const dispatch = useDispatch();
  const formData = new FormData()
  const handleNewReport = (values) => {
    const profile=localStorage.getItem("profile_id")
    if (values) {
      formData.append("name", values["name"])
      formData.append("summary", values["summary"])
      formData.append("profile", values[profile])
      formData.append("report", values["report"])
      formData.append("report_against", values["report_against"])

      console.log("fomData", formData)
      dispatch(report.post_employee_report(formData))
        .then(res => {
          toast.success("Report added successfully.")
          console.log("Helllo WOOOO")
        })
    } else {
      toast.error("Please fill the required field.")
    }
  }

  return (
    <Box p={2}>
      <Formik
        initialValues={{}}
        onSubmit={handleNewReport}
        validationSchema={Yup.object().shape({
          name: Yup.string().required("Full Name is required."),
          summary: Yup.string().required("Summary is required."),
        })}
      >
        {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          touched,
          values,
          setFieldValue
        }) => (
          <form onSubmit={handleSubmit} >
            <Grid container spacing={6}>
              <Grid item xs={12} sm={12} md={7}>
                <Box>
                  <Typography>Report Name <span style={{ color: 'red' }}>*</span></Typography>
                  <TextField
                    fullWidth
                    name="name"
                    margin="normal"
                    placeholder="Name..."
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.name && errors.name)}
                    helperText={touched.name && errors.name}
                  />
                </Box>
                <Box mt={2}>
                  <Typography>Summary<span style={{ color: 'red' }}>*</span></Typography>
                  <TextField
                    fullWidth
                    name="summary"
                    margin="normal"
                    placeholder="your report summary .."
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.summary && errors.summary)}
                    helperText={touched.summary && errors.summary}
                  />
                </Box>
                <Box mt={2}>
                  <Typography>Report Against<span style={{ color: 'red' }}>*</span></Typography>
                  <TextField
                    fullWidth
                    name="report_against"
                    margin="normal"
                    placeholder="Report against"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    className={classes.fm__textField}
                    error={Boolean(touched.report_against && errors.report_against)}
                    helperText={touched.report_against && errors.report_against}
                  />
                </Box>
                <Box mt={2}>
                  <Typography>Report<span style={{ color: 'red' }}>*</span></Typography>
                  <Input
                    variant="contained"
                    className={classes.fm__form__btn}
                    id="report"
                    name="report"
                    type="file"
                    onChange={(event) => {
                      setFieldValue("report", event.currentTarget.files[0])
                      setSelectedFile(event.target.files[0])
                    }} />
                  <br />
                  {selectedFile && <img src={preview}
                    alt="image"
                    className="img-thumbnail"
                    height={100}
                    width={100} />}
                </Box>
                <Button
                  variant="contained"
                  fullWidth
                  className={classes.fm__form__btn}
                  type="submit"
                >
                  Add Your Report
                </Button>
              </Grid>
            </Grid>
          </form>
        )}
      </Formik>
    </Box>
  )
}

const mapDispatchToProps = (dispatch) => {
  return {
    post_employee_report: () => dispatch(report.post_employee_report(formData))

  }
}
export default connect(mapDispatchToProps)(AddReportForm)