import { Navigate } from 'react-router-dom';


/** ========================
 * Main Layout
 =========================== */
 
 import MainLayout from 'src/layouts/MainLayout';
 import Login from './pages/auth/Login';
 import ForgotPassword from './pages/auth/ForgotPassword';
 import NotFound from './pages/NotFound';
// import Register from './pages/Register';

/** =========================
 * Dashboard Layout
 ============================ */

import Dashboard from "./pages/Dashboard";
import DashboardLayout from "./layouts/DashboardLayout";
import FamilyMembers from "./pages/FamilyMembers";
import HealthDevices from "./pages/HealthDevices";
import MyDoctors from "./pages/MyDoctors";
import AddReport from "./pages/AddReport";
import AddManuallyVitals from './components/Dashboard/AddManuallyVitals';
import UserDetails from "src/components/Dashboard/UserDetails";
import DoctorView from './components/Doctors/doctorView';
import DoctorProfile from './pages/MyDoctors/doctorProfile';
import MySchedule from './pages/MySchedule';



/** ========================
 * Auth Layout
 =========================== */
import AuthLayout from 'src/layouts/AuthLayout';

const routes = ( isAuthenticated ) => [
  {
    path: '/',
    element: isAuthenticated ? <Navigate to="/app/dashboard" /> : <AuthLayout />,
    children: [
      { path: 'login', element: <Login /> },
      { path: 'reset-password', element: <ForgotPassword /> },
      { path: '404', element: <NotFound /> },
      { path: '/', element: <Navigate to="/login" /> },
      { path: '*', element: <Navigate to="/404" /> },
    ]
  },
  {
    path: 'app',
    element: isAuthenticated ? <DashboardLayout /> : <Navigate to="/login" />,
    children: [
      { path: 'Dashboard', element: <Dashboard /> },
      {path:'familymembers',element:<FamilyMembers />},
      {path:'addreport',element:<AddReport />},
      {path:'mydoctors',element:<MyDoctors />},
      {path:'addVital',element:<AddManuallyVitals />},
      {path:'doctorProfile/:id',element:<DoctorProfile />},
      { path: '*', element: <Navigate to="/404" /> },
      {path:"mySchedule",element:<MySchedule />}
    ]
  },
];

export default routes;