import React from 'react'

/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
    Grid
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Formik Lib ========== */
import { Formik} from "formik"
import * as Yup from "yup";


function BloodPressure({ bloodPressure,setBloodPressure, fun,setOpen }) {
    const hendleSubmit = (val) => {
        const data = {
            id: 4,
            reading1: val.Systolic,
            reading2: val.diastolic,
            value: 'cm'
        }
        console.log("Data", data)
        fun(data)
        setOpen(false)
    }
    return (
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            fullWidth
            maxWidth="sm">
            <DialogTitle>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add Blood Pressure</Typography>
                    <IconButton onClick={() => setOpen(false)}>
                        <CloseIcon />
                    </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent sx={{ paddingTop: 0 }}>
                <Container>
                    <Formik initialValues={{}}
                        onSubmit={hendleSubmit}
                        validationSchema={Yup.object().shape({
                            Systolic: Yup.number().required('Please Enter a Number'),
                            diastolic: Yup.number().required('Please Enter a Number')
                        })}>{({ errors,
                            handleBlur,
                            handleChange,
                            handleSubmit,
                            touched, }) => (
                            <form onSubmit={handleSubmit}>
                                <Box>
                                    <Grid container spacing={3}>
                                        <Grid item xs={12} sm={12} md={6}>
                                            <Box>
                                                <Typography>Systolic<span style={{ color: 'red' }}>*</span></Typography>
                                                <TextField
                                                    name="Systolic"
                                                    margin="normal"
                                                    placeholder="Syatolic"
                                                    onBlur={handleBlur}
                                                    onChange={handleChange}
                                                    error={Boolean(touched.Systolic && errors.Systolic)}
                                                    helperText={touched.Systolic && errors.Systolic} />
                                            </Box>
                                        </Grid>
                                        <Grid item xs={12} sm={12} md={6}>
                                            <Box>
                                                <Typography>Diastolic<span style={{ color: 'red' }}>*</span></Typography>
                                                <TextField
                                                    name="diastolic"
                                                    margin="normal"
                                                    placeholder="diastolic"
                                                    onBlur={handleBlur}
                                                    onChange={handleChange}
                                                    error={Boolean(touched.diastolic && errors.diastolic)}
                                                    helperText={touched.diastolic && errors.diastolic} />
                                            </Box>
                                            <Box>
                                            <Button onClick={()=>{setBloodPressure(false)}}>back</Button>
                                            <Button
                                                variant="contained"
                                                type="submit"
                                            >Submit</Button>
                                            </Box> 
                                        </Grid>
                                    </Grid>
                                </Box>
                            </form>
                        )}
                    </Formik>
                </Container>
            </DialogContent>
        </Dialog>
    )
}
export default BloodPressure