import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { dashboard } from "src/redux/actions"
import UserDetails from "src/components/Dashboard/UserDetails"
import { useParams } from 'react-router-dom'; 

const Dashboard = ({
    fetchUserData,
    fetch_vital_data,
    profile_user,
    all_vital_data,
    fetch_health_Status,
    post_vital_data
}) => {
    const {id}=useParams();
    useEffect(() => {
        fetchUserData(),
        fetch_vital_data()
    }, []);

    return (
        <div>
            {profile_user && <UserDetails profile={profile_user} fetch_data={post_vital_data} />}
        </div>
    )
};

const mapStateToProps = (state) => ({
    profile_user: state.dashboard.profile_user,
    all_vital_data: state.dashboard.all_vital_data,
});

const mapDispatchToProps = (dispatch) => ({
    fetchUserData: () => dispatch(dashboard.fetchUserData()),
    fetch_vital_data: () => dispatch(dashboard.fetch_vital_data()),
    post_vital_data: (data) => dispatch(dashboard.post_vital_data(data)),
});

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard)