import Hidden from '@material-ui/core/Hidden';
const Logo = (props) => (
  <>
    <Hidden smUp>
      <img alt="Logo" src="/static/main_logo.png" width="130px" {...props} />
    </Hidden>
    <Hidden smDown>
      <img alt="Logo" src="/static/main_logo.png" width="150px" {...props} />
    </Hidden>
  </>
);

export default Logo;
