export default function EmployeeAreaChart({ data }) {
  return (
    <ResponsiveContainer className="responsive__chart" >
      <AreaChart
        data={data}
        margin={{
          top: 20,
          right: 50,
          left: 10,
          bottom: 10
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="created_on" />
        <YAxis />
        <Tooltip cursor={{ stroke: '#0D809F', strokeWidth: 2 }} />
        <Area
          type="monotone"
          dataKey="reading1"
          stroke="#0D809F"
          fill="#0D809F"
          activeDot={{ r: 6 }} 
          strokeWidth={2} 
        ></Area>
        <Area
          type="monotone"
          dataKey="reading2"
          stroke="#F47920"
          fill="#F47920"
          strokeWidth={3}
        ></Area>
         <Area
          type="monotone"
          dataKey="status"
          stroke="#F47920"
          strokeWidth={3}
          ><LabelList dataKey="status" position="top" /></Area>
      </AreaChart>
    </ResponsiveContainer>
  );
}