const styles = `
  font-weight: bold;
  font-size: 12px;
  padding: 5px 0px;
  font-style: italic;
`;

export default function AlertSocketService(token, afterReceived){
  this.CALLING_SOCKET_URL = `${process.env.REACT_APP_SOCKET_URL}/doctor_notifications/`

  this.socket = new WebSocket(this.CALLING_SOCKET_URL)

  this.onOpen = this.socket.onopen = (e) => {
    console.info("%cCONNECTED:->", `${styles} color: #2D809F;`, e)
    this.socket.send(JSON.stringify({
      token
    }));
  }

  this.socket.onmessage = (e) => {
    afterReceived(e) 
    console.log("%cRECEIVED:->", `${styles} color: #2D809F;`, e.data)
  }

  this.onClose = this.socket.onclose = (e) => {
    console.info("%cCONNECTION CLOSED:->", `${styles} color: red;`, e)
  }

  this.onError = this.socket.onerror = (e) => {
    console.error("%ERROR:->", `${styles} color: red;`, e)
  }

  this.send = (data) => this.socket.send(JSON.stringify(data));
}

