import React from 'react'
import { GET_DOCTOR_DATA,GET_DOCTOR_PROFILE } from '../constants/MyDoctors';
const DASHBOARD_BASE_URL = `${process.env.REACT_APP_API_URL}`;
import axios from 'axios'

let headers = { 'Content-Type': 'application/json' };

export const fetch_doctors = () => async (dispatch, getState) => {
    try {
        const token = getState().auth.access_token;
        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.get(`${DASHBOARD_BASE_URL}/employee/api/emp-doctor-list/?profile_id=64`, { headers })
            if (response.status == 200) {
                dispatch({ type: GET_DOCTOR_DATA, data: response.data })
            }
        }
    }
    catch (error) {
        throw error
    }
}

export const doctor_profile=(id)=>async(dispatch,getState)=>{
    try{
        const token=getState().auth.access_token;
        if(token){
            headers['Authorization']=`Bearer ${token}`;
            const response=await axios.get(`${DASHBOARD_BASE_URL}/employee/api/doctor_profile/?doctor_id=${id}&types=ourdoc`)
            if(response.status==200){
                dispatch({type:GET_DOCTOR_PROFILE,data:response.data.data})
            }
        }
    }
    catch(error){
        throw error
    }
}

