import {GET_USER_PROFILE_DATA,GET_VITAL_DATA,GET_HEALTH_DATA} from "../constants/dashboard";

const initialState={
    profile_user:null,
    all_vital_data:null,
    health_status:null,
};
export default function dashboard(state=initialState,action){
    switch(action.type){
        case GET_USER_PROFILE_DATA:
            return{...state,profile_user:action.data}
        case GET_VITAL_DATA:
            return {...state,all_vital_data:action.data}
        case GET_HEALTH_DATA:
            return {...state,health_status:action.data}
        default:
            return state;
    }
};               