import React, { useEffect } from 'react'
import { MyDoctors } from "src/redux/actions";
import { connect } from 'react-redux';
import classes from "src/styles/MyDoctors/MyDoctors.module.scss"
import {
  Box,
  Container,
  Typography,
  Button,
  Tab,
  Tabs,
  Grid,
  Avatar,
  ListItem,
  ListItemIcon,
  ListItemText
  
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import { useNavigate } from 'react-router-dom';

import DoctorView from "src/components/Doctors/doctorView"


function myDoctor({
  fetch_doctors,
  doctor_data
}) {
  const navigate = useNavigate();
  useEffect(() => {
    fetch_doctors();
  }, [])

  return (
    <div>
      <Box
        sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}
      >
        {doctor_data && console.log(doctor_data)}
        <Container maxWidth={false} className={classes.od__root}>
          <Box className={classes.od__header} >
            <Typography className={classes.od__title}>
              Doctor
            </Typography>
            <Typography
              sx={{
                display: "flex",
                alignItems: "center",
                color: "gray"
              }}
            >
              <LocationOnIcon
                sx={{
                  marginRight: 1
                }}
              />
              New Delhi
            </Typography>
          </Box>
        </Container>
      </Box>
      
      <Box p={2}>
        <Grid container spacing={3}>
          {doctor_data && doctor_data.data.map((item) => {
            return (

              <Grid item xs={12} sm={12} md={4}  >
                <Box className={classes.od__doc__box} 
                 sx={{
                      backgroundColor: 'background.secondary',
                      minHeight: '100%' }}>
                  <Box display="flex" alignItems="center">
                    <Avatar
                      src={item.profile_pic}
                      className={classes.od_doc_avatar}
                    >
                      doctor
                    </Avatar>
                    <Typography className={classes.title}>
                      {item.name}
                     <Typography>{item.email}</Typography>
                      <span>
                        registration no : {item.registration_no}
                      </span>
                    </Typography>
                  </Box>

                  <Box className={classes.od_doc_details} mt={1}>
                  <ListItem sx={{p: 0}}>
                  <ListItemIcon>
                          <Typography className={classes.list__primary}>Qualification:</Typography>
                        </ListItemIcon>
                        <ListItemText 
                          className={classes.list__secondary} 
                          primary={
                            Boolean(item.degree)
                            ?
                            item.degree
                            :
                            "Doctor has no speciality."
                          } 
                        /> 
                    </ListItem>
                    <ListItem sx={{p: 0}}>
                      <ListItemIcon>
                        <Typography className={classes.list__primary}>Experience:</Typography>
                      </ListItemIcon>
                      <ListItemText className={classes.list__secondary} primary={`${item.experience} year`} />
                    </ListItem>
                  </Box>
                  <Box className={classes.od__btn__box}>
                <Button onClick={() => {navigate({pathname: `/app/doctorProfile/${item.id}`})}} >
                  View Profile
                </Button></Box>
                </Box>
              </Grid>
            )
          })}
        </Grid>
      </Box>
    </div>
  )
}

const mapStateToProps = (state) => ({
  doctor_data: state.MyDoctors.doctor_data
})
const mapDispatchToProps = (dispatch) => ({
  fetch_doctors: () => dispatch(MyDoctors.fetch_doctors())
})

export default connect(mapStateToProps, mapDispatchToProps)(myDoctor)