import axios from 'axios'
import { FETCH_EMPLOYEE_REPORT } from "../constants/report";
import React from "react"

const REPORT_BASE_URL = `${process.env.REACT_APP_API_URL}`


export const fetch_Employee_reports = (data) => async (dispatch, getState) => {
    let headers = { 'Content-Type': 'application/json' }
    const profile_id=localStorage.getItem('profile_id')
    try {
        const token = getState().auth.access_token;

        if (token) {
            headers['Authorization'] = `Bearer ${token}`;
            const response = await axios.get(`${REPORT_BASE_URL}/employee/api/get-reports?profile_id=${profile_id}`, { headers })
            if (response.status == 200) {
                dispatch({ type: FETCH_EMPLOYEE_REPORT, data: response.data.data });
            }
        }
    }
    catch (error) {
        throw error
    }
}

export const post_employee_report=(data)=>async (dispatch,getState)=>{
    let headers= {
        Accept: 'application/json',
        'Content-Type': 'multipart/form-data',
    } 
    try{
        const token=getState().auth.access_token;
        console.log("DATA",data)
       
        if(token){
            headers['Authorization'] = `Bearer ${token}`;
            const response=await axios.post(`${REPORT_BASE_URL}/employee/api/employee-report/`,data,{ headers});
            if(response.status==200){
                console.log("RESPONSE",response);
            }
        }
    }
    catch(error){
        throw error
    }
}