import { FETCH_EMPLOYEE_REPORT } from "../constants/report";

const initialState = {
    employeeReport: null
}

export default function report(state = initialState, action) {
    switch (action.type) {
        case FETCH_EMPLOYEE_REPORT:
            console.log("DATATA",action.data)
            return {
                ...state,employeeReport:action.data}
        default:
            return state;
    }
}