import React, { useState } from 'react';
import { styled } from '@mui/material/styles';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Container from '@mui/material/Container';
import Box from '@mui/material/Box';
import moment from 'moment';
import MenuItem from '@mui/material/MenuItem';
import ChevronRightOutlinedIcon from '@material-ui/icons/ChevronRightOutlined';
import KeyboardArrowLeftOutlinedIcon from '@material-ui/icons/KeyboardArrowLeftOutlined';
import InputLabel from '@mui/material/InputLabel';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import Button from '@mui/material/Button';
import Select from '@mui/material/Select';
import Input from '@mui/material/Input';
import classes from "src/styles/MySchedule/MySchedule.module.scss";
import Grid from '@mui/material/Grid';


const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        fontSize: 13,
        backgroundColor: "white",
        color: "gray",
    },
    [`&.${tableCellClasses.body}`]: {
        fontSize: 14,
        borderBottom: "none",
    },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: "white",
    },
    // hide last border
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));

function SmartCalendar({ response }) {
    const [currentMonth, setCurrentMonth] = useState(response.format("MMMM"));
    const [currentYear, setCurrentYear] = useState(response.format("YYYY"));
    const [showYear, setShowYear] = useState(false)

    // Months
    const months = moment.months(response)
    // Week Days
    const weekDays = moment.weekdaysShort();

    // Days in Month
    const daysInMonth = response.daysInMonth()
    const monthDay = [];
    for (let days = 1; days <= daysInMonth; days++) {
        monthDay.push(days)
    }

    var currentDay = moment().format('D');
    console.log("Day", currentDay)


    //first Empty Days in Month
    const firstDaysInMonth = response.startOf("month").format('d')
    const emptyDays = [];
    for (let eDays = 1; eDays <= firstDaysInMonth; eDays++) {
        emptyDays.push("")
    }
    // total Days in Month
    const totalSlots = [...emptyDays, ...monthDay]

    // Add Days in Month Slots
    let rows = [];
    let cells = [];
    totalSlots.forEach((row, i) => {
        if (i % 7 !== 0) {
            cells.push(row);
        } else {
            let insertRow = cells.slice();
            rows.push(insertRow);
            cells = [];
            cells.push(row)
        }
        if (i === totalSlots.length - 1) {
            let insertRow = cells.slice();
            rows.push(insertRow)
        }
    })

    const hendleMonthChange = (event) => {
        const newData = event.target.value;
        setCurrentMonth(newData)
        let monthNo = moment.months(response).indexOf(newData)
        const newResponse = response.set("month", monthNo)
    }
    const RowsData = () => {
        return (
            rows.map((item, index) =>
                <TableRow key={index} sx={{ border: 0.1, borderColor: "#ddd" }}>
                    {
                        item.map((day, index) => <TableCell key={index} align="center" size="small" className={classes.ms__table_cell} sx={{ border: 0.1, borderColor: "#ddd", color: "gray" }}>
                            {
                                day == currentDay ?
                                    <div style={{ backgroundColor: "#3498db", color: "white", fontWeight: "bolder", borderStyle: "solid", borderRadius: 100 }}>{day}</div> :
                                    <div>{day}</div>
                            }
                        </TableCell>)
                    }
                </TableRow>)
        )
    }
    const SelectMonth = (months) => {
        return (
            <Select variant="standard"
                value={currentMonth}
                onChange={(e) => hendleMonthChange(e)}
                label="Age" disableUnderline
                IconComponent="disable" >
                {moment.months(response).map((days, index) => <MenuItem value={days} key={index * 3} sx={{ color: "gray" }}>{days}</MenuItem>)}
            </Select>
        )
    }
    const onKeyUp = (event) => {
    }
    const onYearChange = (event) => {
        setCurrentYear(event.target.value)
        const newYear = response.set("year", event.target.value)
        console.log("On Year Change", newYear)
    }
    const YearNav = () => {
        return (
            showYear
                ?
                <div style={{ display: "inline" }}>
                    <Input type="number"
                        defaultValue={currentYear}
                        onKeyUp={(e) => onKeyUp(e)}
                        onChange={(e) => onYearChange(e)}
                        placeholder="years"
                        sx={{ width: 60 }}
                    />
                </div>
                :
                <div onClick={() => setShowYear(true)} style={{ display: "inline" }}>{currentYear}</div>
        )
    }
    const preMonth = () => {
        const post = response.subtract(1, "month");
        setCurrentMonth(response.format("MMMM"))
    }
    const nextMonth = () => {
        const pre = response.add(1, "month");
        setCurrentMonth(response.format("MMMM"))
    }
    return <div>
        <Container className={classes.ms__table}>
            <Box sx={{ bgcolor: 'white' }}>
                <Grid container spacing={2}>
                    <Grid container item sm={4}
                        direction="row"
                        justifyContent="left"
                        alignItems="left">
                        <Button size="small" sx={{ color: "#ddd", border: 1 }}><KeyboardArrowLeftOutlinedIcon onClick={() => { preMonth() }} /></Button>
                        <Button size="small" sx={{ color: "#ddd", border: 1 }}> <ChevronRightOutlinedIcon onClick={() => { nextMonth() }} /></Button>
                    </Grid>
                    <Grid container item sm={4}
                        direction="row"
                        justifyContent="center"
                        alignItems="center">
                        <SelectMonth />  <YearNav />
                    </Grid>
                </Grid>
                <TableContainer component="paper">
                    <Table sx={{
                        [`& .${tableCellClasses.root}`]: {
                            p: 3,
                        },
                    }} aria-label="simple table">
                        <TableHead>
                            <TableRow>
                                {
                                    weekDays.map((item, index) => {
                                        return (
                                            <StyledTableCell key={index} align="center">
                                                {item}
                                            </StyledTableCell>)
                                    })
                                }
                            </TableRow>

                        </TableHead>
                        <TableBody>


                            <RowsData />
                        </TableBody>
                    </Table>
                </TableContainer>
            </Box>
        </Container>

    </div>;
}

export default SmartCalendar;
