import React from 'react';
import {
  IconButton,
  Avatar,
  Box,
  Chip
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import { useNavigate } from 'react-router-dom';

import getInitials from './getInitials';
import "src/styles/notificationCard.scss";

export default function ShowNotification({ open, setOpen, cardData }){

  const navigate = useNavigate();

  const redirectToProfile = () => {
    if(cardData){
      navigate(`/app/dashboard/patient-report/${cardData.organisation_id}/${cardData.employee_code}/${cardData.profile_id}`)
      setOpen(false)
    }
  }

  return (
    <>
      {
        open && 
        <div 
          className="tr-floating-card"
        >
          <div className="tr-floating-card__thumbnail">
            {/* <img
              alt="Andy Tran"
              src="https://en.gravatar.com/userimage/64217327/096c4ecedf5cf0259c707b7f5255246d.jpg?size=400"
            /> */}
            <Avatar
              // src="https://en.gravatar.com/userimage/64217327/096c4ecedf5cf0259c707b7f5255246d.jpg?size=400"
              alt={cardData.name}
              sx={{
                mr: 2,
                background: '#2D809F',
                fontSize: 22,
                width: 60,
                height: 60
              }}
            >
              {getInitials(cardData.name)} 
            </Avatar>
          </div>
          <div className="tr-floating-card__content">
            <Box display="flex" justifyContent="space-between" alignItems="center">
              <div>
                <h2 
                  className="tr-floating-card__title"
                  onClick={redirectToProfile}
                >
                  {cardData.name}
                </h2>
                <h3 className="tr-floating-card__sub-title">
                  {cardData.company}. ({cardData.location})
                </h3>
              </div>
              <IconButton
                onClick={() => {
                  setOpen(false)
                }}
              >
                <CloseIcon />
              </IconButton>
            </Box>
            <div>
              <p className="tr-floating-card__description">
                {/* I am a passionate Front-End Web Developer that loves to experiment
                with awesome technology such as React, Redux, Angular, Meteor,
                TweenMax, and many more! */}

                  <Chip 
                    label={cardData.status} 
                    size="small"
                    sx={{
                      background: cardData.status === "alert" 
                        ?  "red" 
                        : cardData.status === "moderate"
                        ? "#F47920"
                        : "limegreen",

                      color: "#fff",
                      textTransform: "capitalize"
                    }} 
                  />
              </p>
            </div>
            {/* <div className="tr-floating-card__footer">
              <a href="https://andytran.me">View Profile</a>
            </div> */}
          </div>
        </div>
      }
    </>
  );
};
