import React from 'react'
import {GET_DOCTOR_DATA,GET_DOCTOR_PROFILE} from "../constants/MyDoctors"

const initialState = {
    doctor_data:null,
    doctor_profile:null,
}

const MyDoctors=(state=initialState,action)=> {
    switch(action.type){
        case GET_DOCTOR_DATA:
            return {...state,doctor_data:action.data}
        case GET_DOCTOR_PROFILE:
            return {...state,doctor_profile:action.data}
        default:
            return state
    }
}



export default MyDoctors