import {useContext} from 'react';
import {
  IconButton,
  Avatar,
  Box,
  Button,
} from '@material-ui/core';
import CloseIcon from '@material-ui/icons/Close';
import SocketContext from "src/context/socket";
import { useSelector } from "react-redux";


// Including all libraries, for access to extra methods.
import encrypter from "src/utils/encrypter"


import getInitials from './getInitials';
import "src/styles/notificationCard.scss";

export default function CallNotificationCard({ open, setOpen, cardData }){

  const { roomRef } =  useContext(SocketContext);
  const token = useSelector(state => state.auth.access_token);
  const user = useSelector(state => state.auth.user);

  const redirectToProfile = () => {
    if(cardData){
      if(roomRef.current){
        roomRef.current.send(
          JSON.stringify({
            token: token,
            kiosk: false,
            connected: true,
            doctor_connect: true,
            doctor_id: user.id,
            room_id: cardData.room.room_id,
            profile_id: cardData.profile.id,
          })
        );
      }

      const data = {
        room_id: cardData.room.room_id,
        agora_token: cardData.room.token,
        agora_channel: cardData.room.channel_name,
        auth: token,
        emp_profile_id: cardData.profile.id,
        doc_profile_id: cardData.doctor.id,
      }

      const encoded = encrypter(data);
      const uri = encodeURIComponent(encoded)

      window.open(`https://meet.tracelyfe.com/meet/${uri}`, '_blank')

      setOpen(false)

    }
  }
  


  return (
    <>
      {
        open && cardData && 
        <div 
          className="tr-floating-card"
        >
          <div className="tr-floating-card__thumbnail">
            {/* <img
              alt="Andy Tran"
              src="https://en.gravatar.com/userimage/64217327/096c4ecedf5cf0259c707b7f5255246d.jpg?size=400"
            /> */}
            <Avatar
              // src="https://en.gravatar.com/userimage/64217327/096c4ecedf5cf0259c707b7f5255246d.jpg?size=400"
              alt={cardData.profile.name}
              sx={{
                mr: 2,
                background: '#2D809F',
                fontSize: 22,
                width: 60,
                height: 60
              }}
            >
              {getInitials(cardData.profile.name)} 
            </Avatar>
          </div>
          <div className="tr-floating-card__content">
            <Box display="flex" justifyContent="space-between" alignItems="center">
              <div>
                <h2 
                  className="tr-floating-card__title"
                  onClick={redirectToProfile}
                >
                  {cardData.profile.name}, {`#${cardData.profile.employee_code}`}
                </h2>
                <h3 className="tr-floating-card__sub-title">
                  {cardData.profile.company}, {cardData.profile.location} 
                </h3>
              </div>
              <IconButton
                onClick={() => {
                  setOpen(false)
                }}
              >
                <CloseIcon />
              </IconButton>
            </Box>
            <div>
              <p className="tr-floating-card__description">
                Mr. {`${cardData.profile.name}`} wants to connect with you on 
                a video call.

                  {/* <Chip 
                    label={cardData.status} 
                    size="small"
                    sx={{
                      background: cardData.status === "alert" 
                        ?  "red" 
                        : cardData.status === "moderate"
                        ? "#F47920"
                        : "limegreen",

                      color: "#fff",
                      textTransform: "capitalize"
                    }} 
                  /> */}
              </p>
             <Button 
                className="button" 
                variant="contained"
                onClick={redirectToProfile}
              >
                Join Now
              </Button>
            </div>
            {/* <div className="tr-floating-card__footer">
              <a href="https://andytran.me">View Profile</a>
            </div> */}
          </div>
        </div>
      }
    </>
  );
};
