import { useState } from "react";
import {
  Box,
  Typography,
  Grid,
  Avatar,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Button,
  Chip
} from "@material-ui/core";

import { useNavigate, createSearchParams } from "react-router-dom";

/* ========= React Redux ========== */
import { useDispatch, useSelector } from "react-redux";
import { family } from "src/redux/actions";

import getInitials from "src/utils/getInitials";

/* ========= Stylesheet ========= */ 
import classes from "src/styles/familyMembers/familyMembers.module.scss"

const FamilyMemberListView = ({ totalFamilyMembers }) => {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const familyMemberProfile = useSelector(state => state.family.familyMemberProfile);
  const [open, setOpen] = useState(false);

  return(
    <Box p={0} className={classes.fm__fam__pad}>
      <Grid container spacing={3}>
       {
         totalFamilyMembers.map((item, index) => (
          <Grid item xs={12} sm={12} md={4} key={index} >
            <Box className={classes.fm__fam__box}>
              <Box display="flex" alignItems="center">
                <Avatar
                  src={item.profile_pic}
                  className={classes.fm_fam_avatar}
                >
                  {getInitials(item.name)}
                </Avatar>
                <Typography className={classes.title}>
                  {item.name}
                  <span>
                    {
                      item.address.length !== 0
                      ?
                      <>
                        {item.address}
                      </>
                      :
                      <>
                        No Address Found
                      </>
                    }
                  </span>
                </Typography>
              </Box>
              <Box className={classes.fm_fam_details} mt={1}>
                <List>
                <ListItem sx={{p: 0}}>
                    <ListItemIcon>
                      <Typography className={classes.list__primary}>Date Of Birth:</Typography>
                    </ListItemIcon>
                    <ListItemText className={classes.list__secondary} primary={`${item.dob}`} />
                  </ListItem>
                  <ListItem sx={{p: 0}}>
                    <ListItemIcon>
                      <Typography className={classes.list__primary}>Gender:</Typography>
                    </ListItemIcon>
                    <ListItemText className={classes.list__secondary} primary={`${item.gender}`} />
                  </ListItem>
                </List>
              </Box>
              <Box className={classes.fm__btn__box}>
                <Button
                  onClick={() => {
                    navigate({
                      pathname: `/app/dashboard/profile/${item.profile_id}`,
                      search: `?${createSearchParams({
                          org: item.profile_id ? false : true
                      })}`
                    })
                  }}
                >
                  Select Profile
                </Button>
              </Box>
            </Box>
          </Grid>
         ))
        }
      </Grid>
    </Box>
  )
}

export default FamilyMemberListView;