import {
    Grid,
    Box,
  } from "@material-ui/core";
  
  /* ======== Tools ======== */
  import { useParams } from "react-router-dom";
  import getParameterByName from "src/utils/getSearchParams";
  import DoctorRightView from "./doctorRightView";
  import DoctorLeftView from "./doctorLeftView";
  
  /* ======== Views ========= */

  
  const DoctorView = ({data}) => {
  
    const { org } = useParams();
    const connect = getParameterByName("connect");
  
    return (
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={4} lg={3}>
          <DoctorLeftView data={data} />
        </Grid>
        <Grid item xs={12} sm={12} md={8} lg={9}>
        <DoctorRightView data={data} />
        </Grid>
      </Grid>
    )
  }
  
  export default DoctorView;