import React from 'react';
/* ========= Material ui ========== */
import {
    Box,
    IconButton,
    Typography,
    TextField,
    MenuItem,
    Button,
    Dialog,
    DialogTitle,
    DialogContent,
    DialogActions,
    Container,
} from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

/* ========= Formik Lib ========== */
import {Formik} from "formik"
import * as Yup from "yup";

function HeartRate({hRate,setHRate,fun}) {
    const hendleSubmit=(val)=>{
        const data={
            id:5,
            reading1:val.Hrate,
            value:'pulse'
        }
        fun(data)
        setHRate(false)
    }
    return (
        <Dialog
            open={open}
            onClose={() => setOpen(false)}
            fullWidth
            maxWidth="sm">
            <DialogTitle>
                <Box display="flex" justifyContent="space-between" alignItems="center">
                    <Typography sx={{ fontSize: "1.3rem", fontWeight: "bold" }}>Add Heart Rate</Typography>
                <IconButton onClick={() => setHRate(false)}>
                    <CloseIcon />
                </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent sx={{ paddingTop: 0 }}>
                <Container>
                <Formik onSubmit={hendleSubmit} initialValues={{}}
                        validationSchema={Yup.object().shape({
                            Hrate: Yup.number().required('Enter Number')
                        })}>
                        {({ errors,
                         handleBlur,
                            handleSubmit,
                            handleChange,
                            touched }) => (
                            <form onSubmit={handleSubmit}>
                                <Box>
                                    <Typography>Add Heart Rate <span style={{ color: 'red' }}>*</span></Typography>
                                    <TextField name="Hrate" margin="normal" onBlur={handleBlur} onChange={handleChange} error={Boolean(touched.Hrate && errors.Hrate)} helperText={touched.Hrate && errors.Hrate} />
                                </Box>
                                <Button
                                    variant="contained"
                                    type="submit"
                                >Submit</Button>
                            </form>
                        )}
                        </Formik>
                </Container>
            </DialogContent>
        </Dialog>
    )
}

export default HeartRate
