import 'react-perfect-scrollbar/dist/css/styles.css';
import { useRoutes } from 'react-router-dom';
import { ThemeProvider, StyledEngineProvider } from '@material-ui/core';
import { useState, useEffect, useRef } from 'react';

// @react-router 
import { useSelector } from "react-redux";
import AlertSocketService from "src/services/AlertSocket";
import SocketContext from "src/context/socket";

import ShowMessage from "src/utils/showMessage"
import GlobalStyles from './components/GlobalStyles';
import ShowNotification from './utils/notificationCard';
import CallNotificationCard from './utils/callNotificationCard';
import theme from './theme';
import routes from './routes';

// Interceptor
import axiosInterceptorHandler from "src/middlewares/axiosInstance";


const App = () => {
  const isAuthenticated = useSelector(state => state.auth.isAuthenticated);
  const token = useSelector(state => state.auth.access_token);
  const user = useSelector(state => state.auth.user)
  const content = useRoutes(routes(isAuthenticated));
  const alertRef = useRef(null);
  const QRRef = useRef(null);
  const roomRef = useRef(null);
  const [notiCardData, setNotiCardData] = useState(null);
  const [open, setOpen] = useState(false);
  const [openNoti, setOpenNoti] = useState(false);
  const [callData, setCallData] = useState(null);
  const [waiting, setWaiting] = useState(null);

  useEffect(() => {
    if(token){
      alertRef.current = new AlertSocketService(token, afterAlertReceive);
      
      // callRef.current = new CallSocketService(token, afterCallReceive);

      // roomRef.current = new RoomSocketService(token, receiver)

      /**
       * 
       * Due to context api we can't use pre-defined 
       * socket service of QR Socket because we have to use onmessage event trigger
       * in child. 
       * 
       * WE ARE USING SOCKET CONNECTION HERE 
       * TO AVOID RECONNECTING ISSUE 
       *  
       */


      QRRef.current = new WebSocket(
        `${process.env.REACT_APP_SOCKET_URL}/doctor/`
      );

      QRRef.current.onopen = (e) => {
        QRRef.current.send(
          JSON.stringify({
            token
          })
        );
      };

      QRRef.current.onerror = (e) => {
        console.log('err', e);
      };

      
    }
  },[token])



  useEffect(() => {

    if(waiting) return;
    // console.log("user_id", user && user)

    // Room Socket to attend call
    if(!roomRef.current){
      const client = new WebSocket(
        `wss://admin.tracelyfe.com/kconnect/`
      );

      // wss://2033-203-192-212-112.ngrok.io/kconnect/
      // wss://admin.tracelyfe.com/kconnect/

      roomRef.current = client; 
      window.client = client;
  
      client.onopen = (e) => {
        roomRef.current.send(
          JSON.stringify({
            token: token,
            kiosk: false,
            employee_connect: false,
            employee_id: user && user.id
          })
        ); 
      };
  
      client.onmessage = (e) => {
        const data = JSON.parse(e.data);
        console.log(data)
        if(data.Employee === "connected"){
          console.log("Employee socket connected")
        } else {
          console.log("Employee ID", data.data)
          setOpenNoti(true)
          setCallData(data.data)
        }
        // if(data.data !== null && data["room_id"] != undefined){
        //   // setOpenNoti(true)
        //   // setCallData(data)
        // }
      }
  
      client.onerror = (e) => {
        console.log('err', e);
      };

      client.onclose = () => {
        if(roomRef.current){
          console.log("ws closed by server")
        } else {
          console.log("ws closed by app component unmount.")
          return;
        }

        if(waiting) return;

        // up a new one rigth way 
        setWaiting(true);
        setTimeout(() => setWaiting(null), 5000)

      }

    }

    return () => {
      roomRef.current = null;
      client.close();
    }

  },[user, waiting])



  const afterAlertReceive = (e) => {
    const data = JSON.parse(e.data)
    setNotiCardData(data.data)
    if(data) {
      setOpen(true)
    }
    console.log(data)
  }

  // const afterCallReceive = (e) => {
  //   const data = JSON.parse(e.data)
  //   const room_id = data.data.room_id;
  //   console.log(room_id);
  //   if(room_id){
  //     // roomRef.current = new RoomSocketService(token, room_id, receiver)
  //   }
  // }

  // const receiver = (e) => {
  //   console.log(e.data)
  //   // const data = JSON.parse(e.data)
  //   // setOpenNoti(true)
  //   // setCallData(data)
  // }

  // Handling axios interceptor to refresh jwt auth token =====
  axiosInterceptorHandler();
  

  return (
    <SocketContext.Provider value={{ QRRef, roomRef }}>
      <StyledEngineProvider injectFirst>
        <ThemeProvider theme={theme}>
          <GlobalStyles /> 
            {content}
            {
              open && 
              <ShowNotification 
                open={open} 
                setOpen={setOpen} 
                cardData={notiCardData} 
              />
            }
            {
              openNoti && 
              <CallNotificationCard 
                open={openNoti} 
                setOpen={setOpenNoti} 
                cardData={callData}
              />
            }
          <ShowMessage />
        </ThemeProvider>
      </StyledEngineProvider>
    </SocketContext.Provider>
  );
};

export default App;
