import { useState, useEffect} from "react";
import { Helmet } from "react-helmet";
import {
  Box,
  Container,
  Typography,
  Button,
  Tab,
  Tabs
} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import { useNavigate, createSearchParams } from "react-router-dom";

/* ========= Tools ========== */
import TabPanel from "src/utils/tabPanel";
import a11yProps from "src/utils/a11yProps";
import getParameterByName from "src/utils/getSearchParams";

/* ========= React Redux ========== */
import { useDispatch, useSelector } from "react-redux";
import { family } from "src/redux/actions";

/* ======= Views ====== */
import AddNewFamilyMemberForm from "src/components/familyMembers/addNewFamilyMemberForm";
import FamilyMemberListView from "src/components/familyMembers/familyMemberList";

/* ======= Stylesheet ======= */

import classes from "src/styles/familyMembers/familyMembers.module.scss"

const FamilyMembers = () => {

  const dispatch = useDispatch();
  const familyMembers = useSelector(state => state.family.familyMembers);
  const query = getParameterByName("tab");
  const navigate = useNavigate();

  const [value, setValue] = useState(0);
  const [addNew, SetAddNew ] = useState(false);

  const handleChange = (event, newValue) => {
    setValue(newValue);
    navigate({
      pathname: window.location.pathname,
      search: `?${createSearchParams({
          tab: newValue
      })}`
    })
  };

  // Kind of callback function for tab state

  useEffect(() => {
    if(query === null){
      navigate({
        pathname: window.location.pathname,
        search: `?${createSearchParams({
            tab: value
        })}`
      })
    } else {

      /**
       *  
       * We have tab number for now 2 in [0, 1] format
       * Now Check if tab number is greater than tab length
       * 
       */
      if(Number(query) >= 2){

        // If tab number is greater than tab length 
        // push the tab to the 0.
        navigate({
          pathname: window.location.pathname,
          search: `?${createSearchParams({
              tab: 0
          })}`
        })
        setValue(0)

      } else {
        setValue(Number(query))
      }
    }
  },[value])

  // Fetch Family Members List 

  useEffect(() => {
    dispatch(family.getFamilyMembers()) //Family Members
  },[])


  return (
    <>
      <Helmet>
        <title>Family Members | TraceLyfe</title>
      </Helmet> 
      <Box
        sx={{
          backgroundColor: 'background.secondary',
          minHeight: '100%',
          py: 3
        }}
      >
        <Container maxWidth={false} className={classes.fm__root}>
          <Box className={classes.fm__header} >
            <Typography className={classes.fm__title}>
              { addNew ? "Add New Family Member" : "Family Member"}
            </Typography>
            <Typography
              sx={{
                display: "flex",
                alignItems: "center",
                color: "gray"
              }}
            >
              <LocationOnIcon 
                sx={{
                  marginRight: 1
                }}
              />
              New Delhi
            </Typography>
          </Box>
          <Box mt={3}>
            <Tabs
              value={value} 
              onChange={handleChange} 
              aria-label="report tabs" 
              variant="scrollable"
              className={classes.tab__root}
            >
              <Tab 
                label="Family Members" 
                {...a11yProps(0)} 
                classes={{
                  root: classes.tabs,
                  selected: classes.tab__selected
                }} 
              />
              <Tab 
                label="Add New Family Member" 
                {...a11yProps(1)} 
                classes={{
                  root: classes.tabs,
                  selected: classes.tab__selected
                }} 
              />

              {/* Just to show full width border  */}

              {/* <span className={classes.tab__extraBorder}>{" "}</span>   */}

            </Tabs>
            <TabPanel value={value} index={0}>
              {
                familyMembers && 
                <FamilyMemberListView totalFamilyMembers={familyMembers} />
              }
            </TabPanel>
            <TabPanel value={value} index={1}>
              <AddNewFamilyMemberForm showFamilyMember={SetAddNew} />
            </TabPanel>
          </Box>
        </Container>
      </Box>  
    </>
  )
}

export default FamilyMembers;