import {
  GET_FAMILY_MEMBERS,
  SELECT_FAMILY_MEMBER,
  UPDATE_PAGE_NUM,
} from "../constants/family";

const initialState = {
  familyMembers: null,
  familyMemberProfile: null,
  pageNum: 1,
}

export default function family( state = initialState, action ){
  switch(action.type){
    
    case GET_FAMILY_MEMBERS:
      return { ...state, familyMembers: action.data }

    case SELECT_FAMILY_MEMBER:
      return { ...state, familyMemberProfile: action.data }

    case UPDATE_PAGE_NUM:
      return {...state, pageNum: action.data}

    default:
      return state;
  }
}